import React from 'react';
import clsx from 'clsx';
import { makeStyles } from '@material-ui/core/styles';
import Drawer from '@material-ui/core/Drawer';
import Button from '@material-ui/core/Button';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import NavMenuItem from "../NavigationMenu/NavMenuItem/NavMenuItem";
import Hamburger from "./Hamburger/Hamburger";


const useStyles = makeStyles({
  list: {
    width: 250,
  },
  fullList: {
    width: 'auto',
  },
});

export default function TemporaryDrawer() {
  const classes = useStyles();
  const [state, setState] = React.useState({
    left: false,
  });

  const toggleDrawer = (anchor, open) => (event) => {
    if (event.type === 'keydown' && (event.key === 'Tab' || event.key === 'Shift')) {
      return;
    }

    setState({ ...state, [anchor]: open });
  };

  const listLinks = (text) => {
    switch (text) {
        case "home": return "#";
        case "about": return "#about";
        case "projects": return "#projects";
        case "contact": return "#contact";
        case "resume": return "https://drive.google.com/file/d/1au6gZ7Aqj2dN6sX3zkneMUjhqRK1AHz3/view?usp=sharing";
        default: return "#";
    }
  }

  const list = (anchor) => (
    <div
      className={clsx(classes.list, {
        [classes.fullList]: anchor === 'top' || anchor === 'bottom',
      })}
      role="presentation"
      onClick={toggleDrawer(anchor, false)}
      onKeyDown={toggleDrawer(anchor, false)}
    >
      <List>
        {['home', 'about', 'projects', 'contact', 'resume'].map((text, index) => (
          <ListItem button key={text}>
            <NavMenuItem link={listLinks(text)}>{text.toUpperCase()}</NavMenuItem>
          </ListItem>
        ))}
      </List>
    </div>
  );

  return (
    <div>
      {['left'].map((anchor) => (
        <React.Fragment key={anchor}>
          <Button onClick={toggleDrawer(anchor, true)}><Hamburger /></Button>
          <Drawer anchor={anchor} open={state[anchor]} onClose={toggleDrawer(anchor, false)}>
            {list(anchor)}
          </Drawer>
        </React.Fragment>
      ))}
    </div>
  );
}